.header {
  height: 54px;
  background-color: #ffffff;
  border-bottom: 1px solid #dbdbdb;
}

.wrapper {
  width: 65%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-button {
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
}

.logo {
  height: 29px;
  margin-top: 10px;
}

.right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.center {
  border: 1px solid #dbdbdb;
  border-radius: 3px;
  background: #fafafa;
  display: flex;
  align-items: center;
}

.search {
  width: 20px;
  height: 20px;
  padding-left: 5px;
}

.input {
  background: none;
  font-size: 0.9rem;
  outline: none;
  border: none;
  color: #262626;
  padding: 3px 10px 3px 3px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.button {
  border: none;
  outline: none;
  background: none;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.icon {
  width: 30px;
  height: 30px;
}

.avatar-button {
  border: none;
  outline: none;
  background: none;
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-left: 22px;
}

.avatar {
  width: 22px;
  height: 22px;
  border-radius: 50%;
}
